@-webkit-keyframes slideup {
0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 0;
}
100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
}
}
@keyframes slideup {
0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 0;
}
100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
}
}
.embedable-footer {
