  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: 1020;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: black;
  color: white;
  font-size: calc(10px + 1.5vh);
}
@media screen and (min-height: 800px) {
.embedable-footer {
      font-size: 1rem;
}
}
.embedable-footer__brand {
    padding-right: 1rem;
    margin-right: 1rem;
    border-right: 2px solid white;
    font-family: "Fira Sans", "Merriweather", Georgia, "Times New Roman", Times, serif;
    font-size: 1.1em;
}
.embedable-footer__lead {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
