    font-size: 0.9em;
}
.embedable-footer__share-btn {
    border: 1px solid transparent;
    height: 2.5em;
    line-height: 2.5em;
    width: 2.5em;
    font-size: 80%;
    text-align: center;
    border-radius: 50%;
}
.embedable-footer__share-btn.active {
      border-color: white;
}
.embedable-footer__share-btn:hover {
      background: rgba(255, 255, 255, 0.1);
}
.embedable-footer .sharing-options {
    position: absolute;
    bottom: 100%;
    right: 0;
    margin: 0.5rem;
}
.embedable-footer .sharing-options__link {
      opacity: 0;
